.subtotal{
    display: flex; 
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px; 
    padding: 20px; 
    background-color: #F3F3F3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}
.subtotal__gift{
    display: flex; 
    align-items: center;
}
.subtotal__gift > input {
    margin-right : 5px; 
}

.subtotal > button {
    background: #f0c14b;
    border: 1px solid; 
    width: 100%; 
    height: 30px;
    border-radius: 2px;
    margin-top: 10px;
    border-color: #a88734 #937e31 #846a29;
    color: #111; 
}